import React from "react";
import PostSubline from "./PostSubline";
import {
  PostTitle,
  PostTitleInitiale,
  PostExcerpt,
  PostExcerptContainer
} from "./styled/styled-post";

const ArticleExcerpt = ({
  id,
  title,
  date,
  excerpt,
  category,
  slug,
  disqus,
  url,
  timeToRead,
  articleLinkHandler,
  categoryLinkHandler
}) => {
  const disqusConfig = {
    url: url,
    identifier: id,
    title: title
  };

  return (
    <PostExcerptContainer>
      <PostTitle>
        <PostTitleInitiale>{title.charAt(0)}</PostTitleInitiale>
        <a href="#" onClick={() => articleLinkHandler(slug)}>
          {title}
        </a>
      </PostTitle>
      <PostSubline
        date={date}
        disqus={disqus}
        config={disqusConfig}
        timeToRead={timeToRead}
        category={category}
        categoryLinkHandler={categoryLinkHandler}
      />
      <PostExcerpt>{excerpt}</PostExcerpt>
    </PostExcerptContainer>
  );
};

export default ArticleExcerpt;
